<template>
  <transition name="picker-fade">
    <div @click="cancel" @touchmove.prevent class="picker" v-show="state===1">
      <transition name="picker-move">
        <div @click.stop class="picker-panel" v-show="state===1">
          <div class="picker-choose border-bottom-1px">
            <span @click="cancel" class="cancel">{{cancelTxt}}</span>
            <span @click="confirm" class="confirm">{{confirmTxt}}</span>
            <h1 class="picker-title">{{title}}</h1>
          </div>
          <div class="picker-content">
            <div class="mask-top border-bottom-1px"></div>
            <div class="mask-bottom border-top-1px"></div>
            <div class="wheel-wrapper" ref="wheelWrapper">
              <div :key="index" class="wheel" v-for="(data,index) in pickerData">
                <ul class="wheel-scroll">
                  <li :key="i" class="wheel-item" v-for="(item,i) in data">{{item.text}}</li>
                </ul>
              </div>
            </div>
          </div>
          <div class="picker-footer"></div>
        </div>
      </transition>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll'

const STATE_HIDE = 0
const STATE_SHOW = 1

const COMPONENT_NAME = 'picker'
const EVENT_SELECT = 'select'
const EVENT_VALUE_CHANGE = 'valuechange'
const EVENT_CANCEL = 'cancel'
const EVENT_CHANGE = 'change'

export default {
  name: COMPONENT_NAME,
  props: {
    data: {
      type: Array,
      default () {
        return []
      }
    },
    title: {
      type: String
    },
    cancelTxt: {
      type: String,
      default: 'cancel'
    },
    confirmTxt: {
      type: String,
      default: 'confirm'
    },
    selectedIndex: {
      type: Array,
      default () {
        return []
      }
    },
    value: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      state: STATE_HIDE,
      pickerData: this.data.slice(),
      pickerSelectedIndex: this.selectedIndex,
      pickerSelectedVal: [],
      pickerSelectedText: []
    }
  },
  created () {
    if (!this.pickerSelectedIndex.length) {
      this.pickerSelectedIndex = []
      for (let i = 0; i < this.pickerData.length; i++) {
        this.pickerSelectedIndex[i] = 0
      }
    }
  },
  methods: {
    confirm () {
      if (!this._canConfirm()) {
        return
      }
      this.hide()

      let changed = false
      for (let i = 0; i < this.pickerData.length; i++) {
        let index = this.wheels[i].getSelectedIndex()
        this.pickerSelectedIndex[i] = index

        let value = null
        if (this.pickerData[i].length) {
          value = this.pickerData[i][index].value
        }
        if (this.pickerSelectedVal[i] !== value) {
          changed = true
        }
        this.pickerSelectedText[i] = this.pickerData[i][index].text
      }

      this.$emit(
        EVENT_SELECT,
        this.pickerSelectedVal,
        this.pickerSelectedIndex,
        this.pickerSelectedText
      )

      if (changed) {
        this.$emit(
          EVENT_VALUE_CHANGE,
          this.pickerSelectedVal,
          this.pickerSelectedIndex,
          this.pickerSelectedText
        )
      }
    },
    cancel () {
      this.hide()
      this.$emit(EVENT_CANCEL)
    },
    show () {
      if (this.state === STATE_SHOW) {
        return
      }
      this.state = STATE_SHOW

      if (!this.wheels || this.dirty) {
        this.$nextTick(() => {
          this.wheels = []
          let wheelWrapper = this.$refs.wheelWrapper
          for (let i = 0; i < this.pickerData.length; i++) {
            this._createWheel(wheelWrapper, i)
          }
          this.dirty = false
        })
      } else {
        for (let i = 0; i < this.pickerData.length; i++) {
          this.wheels[i].enable()
          this.wheels[i].wheelTo(this.pickerSelectedIndex[i])
        }
      }
    },
    hide () {
      this.state = STATE_HIDE

      for (let i = 0; i < this.pickerData.length; i++) {
        this.wheels[i].disable()
      }
    },
    setData (data) {
      this.pickerData = data.slice()
      this.dirty = true
    },
    setSelectedIndex (index) {
      this.pickerSelectedIndex = index
    },
    refill (datas) {
      let ret = []
      if (!datas.length) {
        return ret
      }
      datas.forEach((data, index) => {
        ret[index] = this.refillColumn(index, data)
      })
      return ret
    },
    refillColumn (index, data) {
      if (this.state !== STATE_SHOW) {
        console.error('can not use refillColumn when picker is not show')
        return
      }
      const wheelWrapper = this.$refs.wheelWrapper
      let scroll = wheelWrapper.children[index].querySelector('.wheel-scroll')
      let wheel = this.wheels[index]
      if (scroll && wheel) {
        let oldData = this.pickerData[index]
        this.$set(this.pickerData, index, data)
        let selectedIndex = wheel.getSelectedIndex()
        let dist = 0
        if (oldData.length) {
          let oldValue = oldData[selectedIndex].value
          for (let i = 0; i < data.length; i++) {
            if (data[i].value === oldValue) {
              dist = i
              break
            }
          }
        }
        this.pickerSelectedIndex[index] = dist
        this.$nextTick(() => {
          // recreate wheel so that the wrapperHeight will be correct.
          wheel = this._createWheel(wheelWrapper, index)
          wheel.wheelTo(dist)
        })
        return dist
      }
    },
    scrollTo (index, dist) {
      const wheel = this.wheels[index]
      this.pickerSelectedIndex[index] = dist
      wheel.wheelTo(dist)
    },
    refresh () {
      this.$nextTick(() => {
        this.wheels.forEach((wheel, index) => {
          wheel.refresh()
        })
      })
    },
    _createWheel (wheelWrapper, i) {
      if (!this.wheels[i]) {
        this.wheels[i] = new BScroll(wheelWrapper.children[i], {
          wheel: {
            selectedIndex: this.pickerSelectedIndex[i],
            /**  */
            wheelWrapperClass: 'wheel-scroll',
            wheelItemClass: 'wheel-item'
          },
          probeType: 3
        })

        this.wheels[i].on('scrollEnd', () => {
          this.$emit(EVENT_CHANGE, i, this.wheels[i].getSelectedIndex())
        })
      } else {
        this.wheels[i].refresh()
      }

      return this.wheels[i]
    },
    _canConfirm () {
      return this.wheels.every(wheel => {
        return !wheel.isInTransition
      })
    }
  },
  watch: {
    data (newData) {
      this.setData(newData)
    }
  }
}
</script>

<style scoped lang="less">
@import '../../style/mixin.less';

.border-top-1px {
  &::before {
    border-top: 1px solid #ebebeb; /*no*/
  }
}

.border-right-1px {
  &::after {
    border-right: 1px solid #ebebeb; /*no*/
  }
}

.border-bottom-1px {
  &::after {
    border-bottom: 1px solid #ebebeb; /*no*/
  }
}

.border-left-1px {
  &::before {
    border-left: 1px solid #ebebeb; /*no*/
  }
}

.picker {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  overflow: hidden;
  text-align: center;
  font-size: 38px;
  background-color: rgba(37, 38, 45, 0.4);

  &.picker-fade-enter,
  &.picker-fade-leave-active {
    opacity: 0;
  }

  &.picker-fade-enter-active,
  &.picker-fade-leave-active {
    transition: all 0.3s ease-in-out;
  }

  .picker-panel {
    position: absolute;
    z-index: 600;
    bottom: 0;
    width: 100%;
    height: 685px;
    background: #fff;

    &.picker-move-enter,
    &.picker-move-leave-active {
      transform: translate3d(0, 685px, 0);
    }

    &.picker-move-enter-active,
    &.picker-move-leave-active {
      transition: all 0.3s ease-in-out;
    }

    .picker-choose {
      position: relative;
      height: 140px;
      color: #999;

      .picker-title {
        margin: 0;
        line-height: 140px;
        font-weight: normal;
        text-align: center;
        font-size: 40px;
        color: #333;
        border-bottom: 1px solid #ebebeb; /*no*/
      }

      .confirm,
      .cancel {
        position: absolute;
        top: 0;
        padding: 54px;
        font-size: 32px;
      }

      .confirm {
        right: 0;
        color: @main-color;

        &:active {
          color: #5aaaff;
        }
      }

      .cancel {
        left: 0;

        &:active {
          color: #c2c2c2;
        }
      }
    }

    .picker-content {
      position: relative;
      top: 20px;

      .mask-top,
      .mask-bottom {
        z-index: 10;
        width: 100%;
        height: 200px;
        pointer-events: none;
        transform: translateZ(0);
        box-sizing: border-box;
      }

      .mask-top {
        position: absolute;
        top: 0;
        background: linear-gradient(
          to top,
          rgba(255, 255, 255, 0.4),
          rgba(255, 255, 255, 0.8)
        );
        border-bottom: 1px solid #ebebeb; /*no*/
      }

      .mask-bottom {
        position: absolute;
        bottom: 1px; /* no */
        background: linear-gradient(
          to bottom,
          rgba(255, 255, 255, 0.4),
          rgba(255, 255, 255, 0.8)
        );
        border-top: 1px solid #ebebeb; /*no*/
      }
    }

    .wheel-wrapper {
      display: flex;
      padding: 0 34px;

      .wheel {
        .flex-fix();
        height: 515px;
        overflow: hidden;
        font-size: 42px;

        .wheel-scroll {
          padding: 0;
          margin-top: 200px;
          line-height: 118px;
          list-style: none;

          .wheel-item {
            list-style: none;
            height: 115px;
            overflow: hidden;
            white-space: nowrap;
            color: #333;
          }
        }
      }
    }
  }

  .picker-footer {
    height: 20px;
  }
}
</style>



